<template>
  <div>
    <div class='header'>
      <h2 class="logo"></h2>
      <div class="search">
        <div class="search-content">
          
        </div>
      </div>
    </div>
    <swiper loop auto :list="demo06_list" :index="demo06_index" @on-index-change="demo06_onIndexChange"></swiper>
    <div class="vux-demo">
      <img class="alogo" src="../assets/vux_logo.png">
    </div>
    <group title="cell demo">
      <cell title="Vux" value="Cool" is-link></cell>
    </group>
   
  </div>
</template>

<script>
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
const imgList=[
  '../assets/carousel/1218-wx-y-700-270.jpg',
  '../src/assets/carousel/700x270gift_lyx_1218.jpg'
];
const demoList = imgList.map((one, index) => ({
  url: 'javascript:',
  img: one
}))
export default {
  components: {
    Swiper,
    SwiperItem,
    Group,
    Cell,
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: 'Hello World!',
      demo06_index: 0,
      demo06_list: demoList
    }
  },
  methods:{
    demo06_onIndexChange (index) {
      this.demo06_index = index
    }
  }
}
</script>

<style scoped>
.vux-demo {
  text-align: center;
}
.alogo {
  width: 100px;
  height: 100px;
}
.header{
  box-sizing: border-box;
  width: 100%;
  position: relative;
  top: 0;
  height: 44px;
  background: -webkit-linear-gradient(top,rgba(0,0,0,.7),rgba(0,0,0,0));
  padding: 0 10px

}
.logo{
  position: absolute;
  left: 0;
  top: 0;
  height: 44px;
  width: 40px;
  background: url('../assets/dd-logo.png') center center no-repeat;
  background-size: 30px 21px;
  margin-left: 10px;
    
}
.search{
  position: relative;
  background: #fff;
  height: 100%;
  margin: 0px 50px;
  box-sizing: border-box;
  background-color: transparent;
}
.search:before{
  content: '';
  display: table;
}
.search-content{
  width: 100%;
  height: 30px;
  background: #fff;
  border-radius: 1rem;
  margin-top: 7px;
}
</style>


